.searchbar {
    margin: 24px 0 0;

    input {
        height: 32px;
        width: 200px;
        padding: 0 8px;
        background-color: $color-bg;
        border: 1px solid $color-primary;
        color: $color-primary;
        text-align: center;
        border-radius: 999px;
        &:focus-visible {
            border: none;
        }
    }
}